import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentUser: {
      id: -1,
      name: "",
      account: "",
      isAdmin: false,
    },
    loginPage: false,
    isLogin: false,
    aboutUser: [],
    shoppingCart: [],
    favoriteItem: [],
  },
  mutations: {
    changeLoginState(state) {
      state.loginPage = !state.loginPage;
    },
    aboutUser(state, userInfo) {
      state.aboutUser = userInfo;
    },
    changeUser(state) {
      state.isLogin = !state.isLogin;
    },
    register(state, userInfo) {
      let userArray = JSON.parse(localStorage.getItem("userInfo")) || [];
      userArray.push(userInfo);
      localStorage.setItem(userInfo.account, JSON.stringify(userArray));
    },
    updateMessage(state, account) {
      state.currentUser[0].account = account;
    },
    setShoppingCart(state, data) {
      state.shoppingCart.push(data);
    },
    deleteShoppingCartOrder(state, index) {
      state.shoppingCart.splice(index, index + 1);
    },
    setFavoriteItem(state, data) {
      state.favoriteItem.push(data);
    },
    deleteFavoriteItem(state, index) {
      state.favoriteItem.splice(index, index + 1);
    },
    deleteFavorite(state, index) {
      state.favoriteItem.pop(index);
    },
    setCurrentUser(state, currentUser) {
      state.currentUser = {
        ...state.currentUser,
        ...currentUser,
      };
    },
  },
  actions: {},
  getters: {
    getShoppingCart(state) {
      return state.shoppingCart;
    },
    getFavoriteItem(state) {
      return state.favoriteItem;
    },
    getloginState(state) {
      return state.isLogin;
    },
    getIsLogin(state) {
      return state.loginPage;
    },
    getWhichUser(state) {
      return state.aboutUser;
    },
    getUserInfo: () => (account) => {
      return JSON.parse(localStorage.getItem(account));
    },
  },
});
