import { apiHelper } from "./../utils/helpers";
const getToken = () => localStorage.getItem("token");

export default {
  getProducts() {
    return apiHelper.get(`/products/`, {
      headers: { Authorization: `Bearer ${getToken()}` },
    });
  },
  getSingleProduct({ productId }) {
    return apiHelper.get(`/products/${productId}`, {
      headers: { Authorization: `Bearer ${getToken()}` },
    });
  },
  getProductsByCount() {
    return apiHelper.get(`products?limit=12`, {
      headers: { Authorization: `Bearer ${getToken()}` },
    });
  },
  getJewelery() {
    return apiHelper.get(`/products/category/jewelery`, {
      headers: { Authorization: `Bearer ${getToken()}` },
    });
  },
  getElectronic() {
    return apiHelper.get(`/products/category/electronics`, {
      headers: { Authorization: `Bearer ${getToken()}` },
    });
  },
};
